import prototype from './prototype.md'
import prototype002 from './prototype002.md'
export const components = {   
    prototype:{ id: 1 , name: prototype, type :'js' , describe:'JavaScript',date:'2021-11-22'},
    prototype002:{ id: 2 ,name: prototype002 , type :'js' , describe:'JavaScript2222', date:'2021-11-22'},
};
// 
export const install = function(Vue, opts = {}) { 
    Object.keys(components).forEach((key) => { 
        // console.log(key);
        // console.log(components[key].name);
        Vue.component(key, components[key].name);
    });
};


